export function randomMovie() {
    const movies = [
            "Waking Life",        
            "Fantastic Planet",
            "Stalker",        
            "Ponyo",        
            "Star Wars: Episode IV",        
            "Blade Runner",        
            "Dr. Strangelove",        
            "Akira",        
            "THX 1138"       
    ]

    const movie = movies[Math.floor(Math.random()*movies.length)];

    return movie;
}


